.s{
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 2px solid rgb(243, 242, 242);
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
}

.s-browser{
    height: 20px;
    background-color: rgb(243, 242, 242);    
    display: flex;
    align-items: center;
}

.s-circle{
    width: 6px;
    height: 6px;
    border-radius: 50%;
    margin: 3px;
    background-color: white;
}

.s-img{
    width: 100%;
}
